.btn {
    position: absolute;
    padding: 12px 24px;
    font-size: 16px;
    font-weight: bold;
    text-transform: uppercase;
    color: #fff;
    background-color: #f44336;
    border: none;
    border-radius: 50px;
    cursor: pointer;
    z-index: 1;
    transition-duration: 0.3s;
  }
  
  .btn.running {
    transform: translate3d(100px, 0, 0);
  }
  